.preload-wrapper {
  position: fixed;
  width: 100px;
  height: 60px;
  margin: auto;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.preload-percent {
  width: 100%;
  height: 100%;
  padding-top: 10px;
  padding-left: 30px;
}

.preload-inner {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 14px;
  color: #ffffff;
  background-color: #ff5000;
  text-align: center;
  line-height: 50px;
}

.preload-activity {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  display: block;
  width: 100px;
  height: 60px;
  border-radius: 50%;
  border: 5px solid #ff5000;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

.preload-activity:before, .preload-activity:after {
  content: '';
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  display: block;
  width: 100px;
  height: 60px;
  border-radius: 50%;
  border: 5px solid #ff5000;
  position: absolute;
  top: -5px;
  left: -5px;
  -webkit-transform: rotate(120deg);
      -ms-transform: rotate(120deg);
          transform: rotate(120deg);
}

.preload-activity:after {
  -webkit-transform: rotate(60deg);
      -ms-transform: rotate(60deg);
          transform: rotate(60deg);
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(359deg);
              transform: rotate(359deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(359deg);
              transform: rotate(359deg);
  }
}


