.detector-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.detector-sight {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.detector-sight-notice {
  text-align: center;
  font-size: .24rem;
  width: 3rem;
  margin: 1rem auto;
  padding: .1rem 0;
  border-radius: .5rem;
  color: #ffffff;
  background-color: #ff4200;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #ff9000), to(#ff5000));
  background-image: -webkit-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: -o-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: linear-gradient(to right, #ff9000 0, #ff5000 100%);
}

.detector-sight-body {
  position: absolute;
  width: 5.6rem;
  height: 5.6rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-2.8rem, -2.8rem);
      -ms-transform: translate(-2.8rem, -2.8rem);
          transform: translate(-2.8rem, -2.8rem);
}

.detector-sight-item {
  position: absolute;
  width: .6rem;
  height: .6rem;
  border: 3px solid #ff5000;
  border-radius: 3px;
}

.detector-sight-item.sight-item-lt {
  left: 0;
  top: 0;
  border-right: none;
  border-bottom: none;
}

.detector-sight-item.sight-item-rt {
  right: 0;
  top: 0;
  border-left: none;
  border-bottom: none;
}

.detector-sight-item.sight-item-lb {
  left: 0;
  bottom: 0;
  border-right: none;
  border-top: none;
}

.detector-sight-item.sight-item-rb {
  right: 0;
  bottom: 0;
  border-left: none;
  border-top: none;
}

.detector-sight-scan {
  width: 5.5rem;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-2.75rem, -2.75rem);
      -ms-transform: translate(-2.75rem, -2.75rem);
          transform: translate(-2.75rem, -2.75rem);
  background-color: #ff4200;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #ff9000), to(#ff5000));
  background-image: -webkit-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: -o-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: linear-gradient(to right, #ff9000 0, #ff5000 100%);
  -webkit-box-shadow: 0 -8px 30px #ff5000;
          box-shadow: 0 -8px 30px #ff5000;
  -webkit-animation: scan 2.5s infinite;
          animation: scan 2.5s infinite;
}

@-webkit-keyframes scan {
  from {
    -webkit-transform: translate(-2.75rem, -2.75rem);
            transform: translate(-2.75rem, -2.75rem);
  }
  to {
    -webkit-transform: translate(-2.75rem, 2.75rem);
            transform: translate(-2.75rem, 2.75rem);
  }
}

@keyframes scan {
  from {
    -webkit-transform: translate(-2.75rem, -2.75rem);
            transform: translate(-2.75rem, -2.75rem);
  }
  to {
    -webkit-transform: translate(-2.75rem, 2.75rem);
            transform: translate(-2.75rem, 2.75rem);
  }
}



